<template>
  <div>
    <h2>{{ circleRange }}km ì´ë‚´ì˜ ì—…ì²´</h2>
    <div id="map" />

    <span style="display : none">
      {{ getSeletedUser }}
    </span>
  </div>
</template>
<script>

import {mapGetters,mapState} from "vuex"
export default {
  computed : {
    ...mapGetters('selectedUser',['getSeletedUser']),
    ...mapState('selectedUser',['circleRange'])
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=06e188354acd0497abcbf63df45c994f&libraries=clusterer,services";
      document.head.appendChild(script);
    }
  },
  updated() {
    document.getElementById('map').innerHTML = "";
    this.initMap()
  },
  methods: {
    async initMap() {
      const latLng = new kakao.maps.LatLng(this.getSeletedUser.lat, this.getSeletedUser.lng)
      const container = document.getElementById("map");
      const options = {
            center: latLng,
            level: 11,
        };
      const map = new kakao.maps.Map(container, options)
      new kakao.maps.Marker({
                position :latLng,
                title : this.getSeletedUser.siteName ,
                map,
            });
      var clusterer = new kakao.maps.MarkerClusterer({
        map: map, // ë§ˆì»¤ë“¤ì„ í´ëŸ¬ìŠ¤í„°ë¡œ ê´€ë¦¬í•˜ê³  í‘œì‹œí•  ì§€ë„ ê°ì²´
        averageCenter: true, // í´ëŸ¬ìŠ¤í„°ì— í¬í•¨ëœ ë§ˆì»¤ë“¤ì˜ í‰ê·  ìœ„ì¹˜ë¥¼ í´ëŸ¬ìŠ¤í„° ë§ˆì»¤ ìœ„ì¹˜ë¡œ ì„¤ì •
        minLevel: 10 // í´ëŸ¬ìŠ¤í„° í•  ìµœì†Œ ì§€ë„ ë ˆë²¨
    });
    var imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";
    var imageSize = new kakao.maps.Size(24, 35);
    var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);
        var markers = this.getSeletedUser.siteList.map(function(i) {
            return new kakao.maps.Marker({
                image : markerImage,
                title : `${i.SITE_NAME} ${String(i.DIST).slice(0,4)}km ë–¨ì–´ì ¸ ìˆìŒ`,
                position : new kakao.maps.LatLng(i.LAT, i.LNG)
            });
        });
    clusterer.addMarkers(markers);
    }
  }
}
</script>
<style scoped>
#map {
  width:100%;
  height:350px;
}
</style>
